* {
  margin: 0;
  padding: 0;

}

@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;500;600&display=swap');
//@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@200;300;400;500;600&display=swap');
@font-face {
  font-family: 'Montserrat';
  src: url('../fonts/Montserrat-Light.otf') ;
}



input {
  border: 0;
  outline: none;
  -webkit-appearance: none;
}

textarea {
  border: 0;
  outline: none;
  resize: none;


}

a {
  text-decoration: none;
  color: black;
  -webkit-tap-highlight-color: transparent;
}

ul {
  list-style-type:none;
}





$one_size: 820px;
$sidebar_turn_off: 600px;
$mobile_version_nav: 550px;
$main_background_color:#F2EFE9;
$second_color:#DB4F2E;


 html,body {
  box-sizing: border-box;
}
body{
  font-family: 'Montserrat', sans-serif;
  background: $main_background_color;
  overflow-x: hidden;
  animation: smooth_appearance 0.6s ease-in;

  &::-webkit-scrollbar {
    width: 12px;
  }
  &::-webkit-scrollbar-thumb {
    background-color: #5f6062;
  }
}

.sidebar {
  height: 100%;
  width: 30px;
  position: fixed;
  bottom: 0;
  background: $main_background_color;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  flex-direction: column;
  z-index: 10;
  @media only screen and (max-height: $sidebar_turn_off) {
    display: none;
  }

  @media only screen and (max-width: 450px) {
    width: 15px;
  }

  .line {
    width: 2px;
    height: inherit;
    background: $second_color;
  }

  .dot_link {
    position: absolute;
    top:194px;
    right: -7px;
    background: $main_background_color;
    transition: all 0.3s ease-in-out;
    padding: 5px 0;

    a {
      width: 17px;
      height: 17px;
      display: block;
      border-radius: 50%;
      background: #555555;
      margin: 15px 0;
      transition: 0.2s all ease-in;
      position: relative;
      z-index: 4;
      &:hover {
        transform: scale(1.1);
        background: $second_color;
      }

    }

  }
}

nav {
  position: fixed;
  width: 96%;
  top: 0;
  background: $main_background_color;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 2rem 1rem 2.3rem 2rem;
  z-index: 13;


  @media only screen and (max-width: $one_size) {
    display: flex;
    flex-direction: column;
    left: 50%;
    top: -147px;
    transform: translateX(-50%);
    padding: 10rem 1rem 3rem 1rem;
  }
  @media only screen and (max-width: $mobile_version_nav) {
    width: 89%;
   position: absolute;
    text-align: center;
  }
  @media only screen and (max-height: 420px) {
    width: 90%;
    position: absolute;
    text-align: center;
  }


  .nav-name {
    font-weight: 500;
    margin-right: 35px;


    @media only screen and (max-width: $one_size) {
      padding-bottom: 0.5rem;
      margin-right: 0;

    }
    h1{
      font-size: 1.9rem;
    }
  }

  .red_line {
    flex: 1;
    padding: 0 1rem;
    background: $second_color;
    height: 2px;

    @media only screen and (max-width: $one_size) {
     display: none;

    }
  }

  .wrap_nav {
    display: flex;
    align-items: center;
    position: relative;
    animation: smooth_appearance 0.6s ease-in;
    .main_nav {
      margin: 0 15px;
      display: flex;
      a {
        padding: 10px 13px;
        font-size:0.9rem;
        display: block;
        color: black;
        text-decoration: none;
        transition: all 0.3s ease-in-out;
        @media only screen and (max-width: 500px) {
          padding: 10px 7px;
        }
        @media only screen and (max-width: 400px) {
          font-size: 0.7rem;
        }
        &:hover {
          color:$second_color;
        }
      }
      .active_nav_menu {
        color: $second_color;

      }

    }

    .social_networks {
      display: flex;
      @media only screen and (max-width: 970px) {
        position: absolute;
        right: 23px;
        bottom: -25px;
      }
      @media only screen and (max-width: $one_size) {
        right: 50%;
        transform: translateX(50%);
        bottom: -30px;

      }

      a {
        width: 32px;
        height: 16px;
        background: #5F6062;
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: -webkit-right;
        margin: 0 5px;
        transition: 0.2s;


        &:before {
          content: "";
          position: absolute;
          top: -10px;
          left: 0;
          width: 0;
          height: 0;
          border-left: 16px solid transparent;
          border-right: 16px solid transparent;
          border-bottom: 10px solid #5F6062;
          transition: 0.2s;
        }

        &:after {
          content: "";
          position: absolute;
          bottom: -10px;
          left: 0;
          width: 0;
          height: 0;
          border-left: 16px solid transparent;
          border-right: 16px solid transparent;
          border-top: 10px solid #5F6062;
          transition: 0.2s;
        }

        &:hover {
          background:$second_color;
        }

        &:hover:before {
          border-bottom: 10px solid $second_color;
        }

        &:hover:after {
          border-top: 10px solid $second_color;
        }

        img {
          width: 15px;
          height: 15px;
        }
      }
    }
  }

}

section {
  max-width: 1400px;
  position: relative;
  background: $main_background_color;
  padding: 0 30px 0 63px;
  margin: 0 auto;
  animation: smooth_appearance 0.6s ease-in;

  @media only screen and (max-height: $sidebar_turn_off) {
    padding: 0 30px 0 30px!important;
  }
  @media only screen and (max-width: 450px) {
    padding: 20px 30px 0 43px;
  }

}
@keyframes smooth_appearance {
  0%{

    opacity: 0;
  }
  100%{

    opacity: 1;
  }

}

#hello {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 1;
  @media only screen and (max-width: $one_size) {
    margin-top: 8rem;
    justify-content: unset;

  }


  .content {
    display: flex;
    flex-direction: column;
    padding: 135px 38px 0 38px;
    @media only screen and (min-width: 1100px) {
      padding: 90px 38px 0 38px;

    }
    @media only screen and (max-width: $one_size) {
      padding: 24px 0 0 0;
    }


    h2 {
      font-size:6rem;
      font-family: 'Playfair Display', sans-serif;
      text-align: center;
      position: relative;
      display: inline-block;
      margin: 26px 0;
      align-self: center;
      @media only screen and (max-width: 650px) {
        font-size: 13vw;
      }
      @media only screen and (max-width: 450px) {
        font-size: 15vw;
      }

      &:before {
        content: '';
        width: 90%;
        height: 4px;
        background: $second_color;
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translateX(-50%);
      }
    }


    p {
      padding-bottom: 3rem;
      font-size: 0.9rem;

    }
  }

}


#home {

  .content-container {
    position: relative;
    margin: 0 auto;

    h2 {

      font-family: 'Playfair Display', sans-serif;
     font-size: 2.5rem;
      text-align: center;
      padding-bottom: 1.5rem;
      @media only screen and (max-width: 650px){
      font-size: 8vw;
      }
      @media only screen and (max-width: 500px) {
        padding-bottom: 0.5rem;
      }
    }


    .description_container {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      margin-bottom: 5rem;
      @media only screen and (max-width: 1110px) {
        flex-direction: column;
      }


      .description_item {
        flex: 0 0 calc(47% - 30px);
        padding: 30px;
        @media only screen and (max-width: 1110px) {
          flex: unset;
          text-align: center;
          margin: 30px 0;
          padding: 0;
        }

        h3 {
          font-family: 'Playfair Display', sans-serif;
          color: #DB4F2E;
          font-weight: 600;
          margin: 0.5rem 0;
          font-size: 1.3rem;

          span {
            font-weight: 400;
          }
        }

        p {
          font-size: 0.9rem;
          padding-top: 1rem;
          text-align: left;

        }

        span {
          margin: 0;
          font-weight: 400;
          font-size: 1.1rem;
        }
      }
    }
  }


}
.projects {
  max-width: 1200px;
  color: black;
  min-height: 100vh;
  margin: 7rem auto 5rem auto;
  @media only screen and (max-width: 1200px) {
    max-width: 700px
  }

  .container_projects_item {
    position: relative;
    margin: 0 auto;
    padding: 105px 22px 0;
    @media only screen and (max-width: $mobile_version_nav) {
      padding: 2.5rem 0 2rem 0;
    }

    h2 {
      color: #DB4F2E;
      font-family: 'Playfair Display', sans-serif;
      font-size: 2rem;
      margin: 0.7rem 0 0.3rem 0;

    }

    h4 {
      color: $second_color;
      margin: 0 0 0.8rem 0;
      font-size: 1rem;

    }

    img {
      width: 100%;
      max-height: 450px;
      object-fit: contain;
      object-position: left;
    }

    p {
      margin: 0;
      font-size: 0.9rem;

    }

  }
}

.photography {
  color: black;
  min-height: 100vh;
  margin-top: 10rem;

  .gallery_photo_list {
    column-count: 4;
    grid-gap: 1px;
    padding: 20px 0 40px 0;
    @media only screen and (max-width: 1110px) {
      column-count: 3;
    }
    @media only screen and (max-width: 768px) {
      column-count: 2;
    }
    @media only screen and (max-width: 500px) {
      column-count: 1;
    }
    .gallery_photo_list_item {
      margin: 0 0 0.6em;
      text-align: center;
      img {
        width: 95%;
        height: auto;
        border-radius: 4px;
        box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.438);

      }
    }
  }


}

.blog {
  height: 100vh;
  margin-top: 10rem;
  .blog-container {
    margin: 0 auto;
    padding: 90px 0 30px 0;
    @media only screen and (max-width: $mobile_version_nav) {
      padding: 30px 0 30px 0;
    }
    span {
      background:$second_color;
      height: 2px;
      width: 60%;
      outline: none;
      border: none;
      margin: 2rem auto;
      display: block;
    }

    .blog-entry {
      text-align: center;

      h1 {
        margin: 0;
        color:$second_color;
        font-family: 'Playfair Display', sans-serif;
        font-size: 1.9rem;
      }

      h3 {
        color: $second_color;
        margin: 0 0 0 0;
      }

      p {
        font-size: .9rem;
        text-align: left;
        margin: 15px 0;

      }
    }
  }
}

.active_dot_link {
  background: $second_color !important;
}
@media only screen and (max-width: $mobile_version_nav) {
  .line_none{
    display: none;
  }
  .indent_correction{
    padding: 20px 30px 0 20px;
  }
}

